}

.resume-a::selection {
  background: #181818;
  color: #fff;
}

.resume-a {
  display: flex;
  align-items: baseline;
}

p {
  margin: 0;
}

.resume {
  margin-right: 1em;
  color: #181818;
  font-size: 1.5em;
  font-weight: bold;
  margin-top: 1em;
  display: inline-block;
  position: relative;
}

.resume:after {
  content: "";
  position: absolute;
  width: 100%;
  -webkit-transform: scaleX(0);
          transform: scaleX(0);
  height: 2px;
  bottom: 0;
  left: 0;
  background-color: #9293eb;
  -webkit-transform-origin: bottom right;
          transform-origin: bottom right;
  transition: -webkit-transform 0.25s ease-out;
  transition: transform 0.25s ease-out;
  transition: transform 0.25s ease-out, -webkit-transform 0.25s ease-out;
}

.resume:hover:after {
  -webkit-transform: scaleX(1);
          transform: scaleX(1);
  -webkit-transform-origin: bottom left;
          transform-origin: bottom left;
}

.icons {
  margin-top: 10em;
}

.icon::selection {
  background: white;
}

.icon {
  margin-right: 20px;
}

.container_home a:link {
